@import url(https://fonts.googleapis.com/css?family=Carter+One);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Carter One', sans-serif;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

a {
  color: #ffffff;
}

/* Homepage */
.Homepage {
  background-image: url(/static/media/Landing-Page.574b6a45.gif);
  background-size: cover;
  margin: 0;
  height: 100vh;
}

.homepage-menu {
  position: relative;
  top: 120px;
}

.homepage-form {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.homepage-form>h1 {
  margin: 0 30px;
}

.homepage-join {
  display: flex;
  flex-direction: column;
}

.homepage-join>input {
  font-size: 15px;
  width: 150px;
  line-height: 1.5em;
}

/* Game.js parent div */
.Game {
  background-size: cover;
  margin: 0;
  height: 100vh;
}

/* Game.js Background */
.backgroundColorR {
  background-image: url(/static/media/bgR.4b1567fa.png);
}

.backgroundColorG {
  background-image: url(/static/media/bgG.1cabe82b.png);
}

.backgroundColorB {
  background-image: url(/static/media/bgB.e00d27fa.png);
}

.backgroundColorY {
  background-image: url(/static/media/bgY.a6a8c435.png);
}

/* UNO Cards */
.Card {
  width: 6rem;
  margin: 2px;
  cursor: pointer;
}

/* Game.js Top Row */
.topInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 60px;
  height: 100px;
}

.topInfo>img {
  width: 7%;
  margin: 0;
  padding: 0;
}

.topInfo>h1 {
  margin-top: 10px;
  font-size: 1.8rem;
  padding-top: 2%;
}

.topInfoText {
  font-size: 2rem;
  margin: 0;
}

/* Player Decks */
.player1Deck {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.player1Deck>img {
  transition: transform 350ms;
}

.player1Deck>img:hover {
  transform: scale(1.08);
  opacity: 1;
}

.player2Deck {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.player2Deck>img {
  transition: transform 350ms;
}

.player2Deck>img:hover {
  transform: scale(1.08);
  opacity: 1;
}

.playerDeckText {
  font-size: 2rem;
  margin: 0 20px;
}

/* Game.js Middle Row */
.middleInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.middleInfo>button {
  align-self: center;
}

/* Game Buttons */
.game-button {
  position: relative;
  top: 0;
  cursor: pointer;
  text-decoration: none !important;
  outline: none !important;
  font-family: 'Carter One', sans-serif;
  font-size: 15px;
  line-height: 1.5em;
  letter-spacing: .1em;
  text-shadow: 2px 2px 1px #0066a2, -2px 2px 1px #0066a2, 2px -2px 1px #0066a2, -2px -2px 1px #0066a2, 0px 2px 1px #0066a2, 0px -2px 1px #0066a2, 0px 4px 1px #004a87, 2px 4px 1px #004a87, -2px 4px 1px  #004a87;
  border: none;
  margin: 15px 15px 30px;
  background: repeating-linear-gradient( 45deg, #3ebbf7, #3ebbf7 5px, #45b1f4 5px, #45b1f4 10px);
  border-bottom: 3px solid rgba(16, 91, 146, 0.5);
  border-top: 3px solid rgba(255,255,255,.3);
  color: #fff !important;
  border-radius: 8px;
  padding: 8px 15px 10px;
  box-shadow: 0 6px 0 #266b91, 0 8px 1px 1px rgba(0,0,0,.3), 0 10px 0 5px #12517d, 0 12px 0 5px #1a6b9a, 0 15px 0 5px #0c405e, 0 15px 1px 6px rgba(0,0,0,.3);
}

.game-button:hover {
  top:2px;
  box-shadow: 0 4px 0 #266b91, 0 6px 1px 1px rgba(0,0,0,.3), 0 8px 0 5px #12517d, 0 10px 0 5px #1a6b9a, 0 13px 0 5px #0c405e, 0 13px 1px 6px rgba(0,0,0,.3);
}

.game-button::before {
  content: '';
  height: 10%;
  position: absolute;
  width: 40%;
  background: #fff;
  right: 13%;
  top: -3%;
  border-radius: 99px;
  }

.game-button::after {
  content: '';
  height: 10%;
  position: absolute;
  width: 5%;
  background: #fff;
  right: 5%;
  top: -3%;
  border-radius: 99px;
  }

.game-button.orange {
  background: repeating-linear-gradient( 45deg, #ffc800, #ffc800 5px, #ffc200 5px, #ffc200 10px);
  box-shadow: 0 6px 0 #b76113, 0 8px 1px 1px rgba(0,0,0,.3), 0 10px 0 5px #75421f, 0 12px 0 5px #8a542b, 0 15px 0 5px #593116, 0 15px 1px 6px rgba(0,0,0,.3);
  border-bottom: 3px solid rgba(205, 102, 0, 0.5);
  text-shadow: 2px 2px 1px #e78700, -2px 2px 1px #e78700, 2px -2px 1px #e78700, -2px -2px 1px #e78700, 0px 2px 1px #e78700, 0px -2px 1px #e78700, 0px 4px 1px #c96100, 2px 4px 1px #c96100, -2px 4px 1px  #c96100;
}

.game-button.orange:hover {
  top:2px;
  box-shadow: 0 4px 0 #b76113, 0 6px 1px 1px rgba(0,0,0,.3), 0 8px 0 5px #75421f, 0 10px 0 5px #8a542b, 0 13px 0 5px #593116, 0 13px 1px 6px rgba(0,0,0,.3);
}

.game-button.red {
  background: repeating-linear-gradient( 45deg, #ff4f4c, #ff4f4c 5px, #ff4643 5px, #ff4643 10px);
  box-shadow: 0 6px 0 #ae2725, 0 8px 1px 1px rgba(0,0,0,.3), 0 10px 0 5px #831614, 0 12px 0 5px #a33634, 0 15px 0 5px #631716, 0 15px 1px 6px rgba(0,0,0,.3);
  border-bottom: 3px solid rgba(160, 25, 23, 0.5);
  text-shadow: 2px 2px 1px #d72d21, -2px 2px 1px #d72d21, 2px -2px 1px #d72d21, -2px -2px 1px #d72d21, 0px 2px 1px #d72d21, 0px -2px 1px #d72d21, 0px 4px 1px #930704, 2px 4px 1px #930704, -2px 4px 1px  #930704;
}

.game-button.red:hover {
  top:2px;
  box-shadow: 0 4px 0 #ae2725, 0 6px 1px 1px rgba(0,0,0,.3), 0 8px 0 5px #831614, 0 10px 0 5px #a33634, 0 13px 0 5px #631716, 0 13px 1px 6px rgba(0,0,0,.3);
}

.game-button.green {
    background: repeating-linear-gradient( 45deg, #54d440, #54d440 5px, #52cc3f 5px, #52cc3f 10px);
    box-shadow: 0 6px 0 #348628, 0 8px 1px 1px rgba(0,0,0,.3), 0 10px 0 5px #2a6d20, 0 12px 0 5px #39822e, 0 15px 0 5px #1d4c16, 0 15px 1px 6px rgba(0,0,0,.3);
    border-bottom: 3px solid rgba(40, 117, 29, 0.5);
    text-shadow: 2px 2px 1px #348628, -2px 2px 1px #348628, 2px -2px 1px #348628, -2px -2px 1px #348628, 0px 2px 1px #348628, 0px -2px 1px #348628, 0px 4px 1px #1d4c16, 2px 4px 1px #1d4c16, -2px 4px 1px #1d4c16;
}

.game-button.green:hover {
  top:2px;
  box-shadow: 0 4px 0 #348628, 0 6px 1px 1px rgba(0,0,0,.3), 0 8px 0 5px #2a6d20, 0 10px 0 5px #39822e, 0 13px 0 5px #1d4c16, 0 13px 1px 6px rgba(0,0,0,.3);
}

/* Spinner */
.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation-fill-mode: both;
  animation: load7 1.8s infinite ease-in-out;
}
.loader {
  color: #ffffff;
  font-size: 5px;
  margin: 0 50px 0 50px;
  position: relative;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader:before {
  left: -3.5em;
  animation-delay: -0.32s;
}
.loader:after {
  left: 3.5em;
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

/* Chat Box */
.chat-box{
	position: absolute;
	bottom: 0px;
	background: white;
	width: 355px;
	border-radius: 5px 5px 0px 0px;
  z-index: 100;
}
.chat-box-player1{
  right: 20px;
}
.chat-box-player2{
  left: 20px;
}
.chat-head{
	width: inherit;
	height: 45px;
	background: #2c3e50;
	border-radius: 5px 5px 0px 0px;
}
.chat-head h2{
	color: white;
	padding-top: 5px;
	display: inline-block;
}
.chat-head span{
	cursor: pointer;
	float: right;
	width: 25px;
	margin: 10px;
}
.chat-body{
  display: none;
	height: 205px;
	width: inherit;
	overflow: hidden auto;
	margin-bottom: 45px;
}
.chat-text{
	position: fixed;
	bottom: 0px;
	height: 45px;
	width: inherit;
}
.chat-text input{
	width: inherit;
	height: inherit; 
	box-sizing: border-box;
	border: 1px solid #bdc3c7;
	padding: 10px;
	resize: none;
  outline: none;
}
.chat-text input:active, .chat-text input:focus, .chat-text input:hover{
	border-color: royalblue;
}
.msg-send{
	background: #406a4b;
}
.msg-receive{
	background: #595080;
}
.msg-send, .msg-receive{
	width: 285px;
	height: 35px;
	padding: 5px 5px 5px 10px;
	margin: 5px auto;
	border-radius: 3px;
	line-height: 30px;
	position: relative;
	color: white;
}
.msg-receive:before{
	content: '';
	width: 0px;
	height: 0px;
	position: absolute;
	border: 15px solid;
	border-color: transparent #595080 transparent transparent;
	left: -29px;
	top: 7px;
}
.msg-send:after{
	content: '';
	width: 0px;
	height: 0px;
	position: absolute;
	border: 15px solid;
	border-color: transparent transparent transparent #406a4b;
	right: -29px;
	top: 7px;
}
.msg-receive:hover, .msg-send:hover{
	opacity: .9;
}

/* Credit text styling */
.credit-text {
  position: absolute;
  top: 20px;
  left: 20px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
  z-index: 10;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.pulse {
  color: white;
  font-weight: 600;
  animation: pulse 2s infinite;
  display: inline-block;
  padding: 0 5px;
}

@keyframes pulse {
  0% {
    opacity: 0.7;
    transform: scale(1);
    text-shadow: 0 0 0 rgba(255, 255, 255, 0.7);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
    text-shadow: 0 0 10px rgba(255, 255, 255, 0.9);
  }
  100% {
    opacity: 0.7;
    transform: scale(1);
    text-shadow: 0 0 0 rgba(255, 255, 255, 0.7);
  }
}

.splash-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #ff5e62, #ff9966);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  transition: all 0.5s ease-in-out;
  opacity: 1;
  transform: scale(1);
  overflow: hidden;
}

.splash-screen.hovered {
  background: linear-gradient(135deg, #ff416c, #ff4b2b);
  transform: scale(1.05);
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.3);
}

.splash-content {
  text-align: center;
  color: white;
  padding: 2rem;
  max-width: 80%;
  animation: fadeIn 1s ease-out;
  position: relative;
  z-index: 2;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.splash-content h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: 2px;
  font-weight: 800;
  text-transform: uppercase;
}

.splash-content p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.countdown {
  font-size: 1.2rem;
  margin: 1.5rem 0;
  font-weight: 500;
  min-height: 1.5rem;
}

.skip-button {
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid white;
  color: white;
  padding: 0.8rem 2rem;
  font-size: 1rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
  outline: none;
  font-weight: 600;
  letter-spacing: 1px;
}

.skip-button:hover {
  background: white;
  color: #ff5e62;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Add some decorative elements */
.splash-screen::before {
  content: '';
  position: absolute;
  width: 200%;
  height: 200%;
  background: var(--card-background) center/contain repeat;
  opacity: 0.1;
  animation: rotate 60s linear infinite;
  z-index: 1;
  pointer-events: none;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .splash-content h1 {
    font-size: 2.5rem;
  }
  
  .splash-content p {
    font-size: 1.2rem;
  }
  
  .countdown {
    font-size: 1rem;
  }
}

/* Ensure the splash screen is above all other content */
.splash-screen {
  z-index: 1000;
  transition: all 0.3s ease-in-out;
}

/* Make sure the content is clickable */
.splash-screen * {
  pointer-events: auto;
}

/* Ensure the button has proper cursor and styling */
.skip-button {
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Improve accessibility */
.splash-screen:focus {
  outline: 2px solid white;
  outline-offset: 4px;
}

/* Add a subtle animation to the content */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.splash-content {
  animation: fadeInUp 0.5s ease-out forwards;
}

.game-rules {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  padding: 20px;
  overflow-y: auto;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  animation: fadeIn 0.3s ease-out;
}

.rules-content {
  background: linear-gradient(135deg, #ff5e62, #ff9966);
  color: white;
  padding: 2rem;
  border-radius: 15px;
  max-width: 800px;
  max-height: 90vh;
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  animation: slideUp 0.4s ease-out;
}

.rules-content h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.rules-content h3 {
  color: white;
  margin: 1.5rem 0 1rem;
  font-size: 1.5rem;
  border-bottom: 2px solid rgba(255, 255, 255, 0.3);
  padding-bottom: 0.5rem;
}

.rules-content h4 {
  color: white;
  margin: 1rem 0 0.5rem;
  font-size: 1.2rem;
}

.rules-content p, .rules-content li {
  line-height: 1.6;
  margin-bottom: 0.8rem;
  font-size: 1.1rem;
}

.rules-content ul, .rules-content ol {
  padding-left: 1.5rem;
  margin-bottom: 1.5rem;
}

.rules-content li {
  margin-bottom: 0.5rem;
}

.rules-scrollable {
  max-height: 60vh;
  overflow-y: auto;
  padding-right: 1rem;
  margin-bottom: 1.5rem;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.5) transparent;
}

.rules-scrollable::-webkit-scrollbar {
  width: 8px;
}

.rules-scrollable::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}

.rules-scrollable::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
}

.special-cards {
  margin: 2rem 0;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
  margin: 1.5rem 0;
}

.card-rule {
  background: rgba(255, 255, 255, 0.1);
  padding: 1.2rem;
  border-radius: 10px;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.2s, box-shadow 0.2s;
}

.card-rule:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.continue-button {
  display: block;
  width: 100%;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid white;
  color: white;
  font-size: 1.2rem;
  border-radius: 50px;
  cursor: pointer;
  margin-top: 1.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
  text-align: center;
}

.continue-button:hover {
  background: white;
  color: #ff5e62;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .rules-content {
    padding: 1.5rem;
    max-width: 95%;
  }
  
  .card-grid {
    grid-template-columns: 1fr;
  }
  
  .rules-content h2 {
    font-size: 2rem;
  }
  
  .rules-content h3 {
    font-size: 1.3rem;
  }
  
  .rules-content p, .rules-content li {
    font-size: 1rem;
  }
}

